import React from 'react';
import styles from './Input.module.css';

class Input extends React.Component {
    constructor(props) {
        super(props);
        // props.setF(this.props.updateData)
    }

    state = {
        error: false
    };

    onChangeTitle = (e) => {
        this.setState({title: e.currentTarget.value, error: false})
    }

    onClickFoEnter = (e) => {
        if (e.key === 'Enter')
            this.props.updateData()
        this.setState({error: true})
    }

    render() {
        return (
            <div className={styles.Input}>
                <input ref={this.props.refLinkInput}
                       className={(this.state.error) ? styles.ErrorRed : styles.InputBlue}
                       onChange={this.onChangeTitle}
                       onKeyPress={this.onClickFoEnter}
                />
            </div>
        );
    };
}

export default Input;
