import React from 'react';
import Name from './Name/Name.jsx';
import styles from './App.module.css';
import Qualitys from "./myQualities/Qualitys";
import InputForm from "./Input/InputForm";

class App extends React.Component {
    state = {
        Names: [],
    };
    refLinkInput = React.createRef()
    updateData = () => {
        if (this.refLinkInput.current.value != "") {
            let ref = "hi, " + this.refLinkInput.current.value;
            let newAr = [...this.state.Names, ref];
            this.setState({Names: newAr});
            this.refLinkInput.current.value = "";
        }
    }

    render() {
        const newNemes =
            this.state.Names.map((el, key) => <div key={key}>{el} </div>)
        return (
            <div className={styles.app}>
                <Name/>
                <Qualitys/>
                <InputForm updateData={this.updateData} refLinkInput={this.refLinkInput}/>
                {newNemes}
            </div>
        );
    }
}

export default App;
